	module.exports = {"data":{"post":{"html":"<p>I was just telling Christopher that Mama is so far away in Australia, that it's already morning there even though it's evening here. He then starts explaining to me that the earth is round, Australia is on the other side of the earth from Germany, and that the earth rotates while the sun stays still. That's why it's already morning in Australia. He rotates his fist as the earth while holding the \"sun\" in his other hand to demonstrate.</p>\n<p>Once he was convinced that I understood, he went back to his dessert.</p>","fields":{"title":"The Wonders of Astronomy","date":"10 Nov 2004","url":"https://papascott.de/2004-11-10-the-wonders-of-astronomy/"}}},"pathContext":{"slug":"/2004-11-10-the-wonders-of-astronomy/","prev":{"fields":{"title":"Singles night at Wal-Mart in Germany","date":"10 Nov 2004","slug":"/2004-11-10-singles-night-at-walmart-in-germany/"}},"next":{"fields":{"title":"Arafat is dead","date":"11 Nov 2004","slug":"/2004-11-11-arafat-is-dead/"}}}}

